<script>
import Pagination1 from "@/components/Pagination1/Pagination1.vue";
import tableComponent from "@/components/Table/Table.vue";

const toLower = (text) => {
  return text.toString().toLowerCase();
};
// 搜尋條件
const searchByName = (items, term) => {
  if (term) {
    return items.filter(
      (item) =>
        toLower(item.project).includes(toLower(term)) ||
        toLower(item.project_Name).includes(toLower(term)) ||
        toLower(item.createDate).includes(toLower(term)) ||
        toLower(item.workSpace).includes(toLower(term)) ||
        toLower(item.status).includes(toLower(term)) ||
        toLower(item.effect).includes(toLower(term))
    );
  }
  return items;
};

export default {
  name: "dataManagement-component",
  components: { Pagination1, tableComponent },
  data() {
    return {
      loading: true,
      search: "",
      tableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "project",
          label: "緊急搶修單編號(緊急搶修名稱)",
          width: "250",
        },
        {
          id: "createDate",
          label: "建立日期 (開始 ~ 結束日期)",
          width: "250",
        },
        {
          id: "workSpace",
          label: "工作區域",
          width: "250",
        },
        {
          id: "status",
          label: "緊急搶修狀態",
          width: "250",
        },
        {
          id: "deleted",
          label: "可用狀態",
          width: "250",
        },
      ],
      data: [],
      tableData: [],
      tableList: [],
    };
  },
  computed: {},
  methods: {
    searchHandler(evt) {
      this.$store.state.currentPage = 1;
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        const val = evt.target.value;
        this.tableData = searchByName(this.data, val);
        this.tableList = this.tableData.slice(0, 10);
      }
    },
    getUrgentList() {
      this.$store
        .dispatch("getUrgentList")
        .then((res) => {
          let data = res.data.data;
          this.data = JSON.parse(JSON.stringify(data));
          this.data.forEach((e, index) => {
            e.index = index + 1;
            e.project = `${e.project_No}(${e.project_Name})`;
            e.createDate = `${e.create_Date}(${e.begin_Date}~${e.end_Date})`;
            e.workSpace = `${e.ld1}/${e.ld2}/${e.ld3}/${e.ld4}`;
            e.status = this.convertStatus(e.project_Status);
            e.effect = this.getEffect(e.deleted);
          });
          // console.log(this.data);
          this.tableData = JSON.parse(JSON.stringify(this.data));
          this.tableList = this.tableData.slice(0, 10);
        })
        .catch((err) => console.log(err));
    },
    async dialogDeleteHandler(item) {
      try {
        const obj = {
          Project_No: item.project_No,
          Status: item.deleted === "0" ? "1" : "0",
        };
        await this.$store.dispatch("updUrgentStatus", obj);
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message: `更改成功`,
        });
        this.getUrgentList();
        this.$store.state.currentPage = 1;
        this.search = "";
      } catch (err) {
        console.log(err);
      }
    },
    changePage(page) {
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
      this.$store.state.currentPage = page;
    },
    openDetails(prjNo) {
      this.getViewSignOffDetail(prjNo);
    },
    async getViewSignOffDetail(prjNo) {
      try {
        const res = await this.$store.dispatch("getViewSignOffDetail", prjNo);
        const data = res.data.data;
        const { href } = this.$router.resolve({
          path: "/emergencyRepairOrderViewSighOffDetails",
          query: {
            data: JSON.stringify(data),
          },
        });
        window.open(href, "_blank");
      } catch (err) {
        console.log(err);
      }
    },
    async openPrint(prjNo) {
      try {
        const res = await this.$store.dispatch("getPrintFormInfo", prjNo);
        const data = res.data.data;
        const { href } = this.$router.resolve({
          path: "/emergencyRepairOrderPrint",
          query: {
            data: JSON.stringify(data),
          },
        });
        window.open(href, "_blank");
      } catch (err) {
        console.log(err);
      }
    },
    async getPrintFormInfo(prjNo) {
      try {
        //const res = await this.$store.dispatch("getPrintFormInfo", prjNo);
        //const data = res.data.data;
        const { href } = this.$router.resolve({
          path: "/emergencyRepairOrderViewSignOffDetails",
          query: {
            data: prjNo, //JSON.stringify(data),
          },
        });
        window.open(href, "_blank");
      } catch (err) {
        console.log(err);
      }
    },
  },
  async mounted() {
    this.$store.state.currentPage = 1;
    this.getUrgentList();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./dataManagement.html"></template>
